import { NextFunction, Response } from 'express';
import { Types } from 'mongoose';
import { SupportedDataSourcesModel } from '../models';
import { AuthRequest } from '../utils';

export const getSupportedDataSources = async (req: AuthRequest, res: Response, next: NextFunction) => {
  try {
    const supportedDataSources = await SupportedDataSourcesModel.find().exec();

    res.status(200).json(supportedDataSources);
  } catch (error) {
    next(error);
  }
};

export const deleteSupportedDataSourceById = async (req: AuthRequest, res: Response, next: NextFunction) => {
  const id = new Types.ObjectId(req.params.id);

  try {
    await SupportedDataSourcesModel.deleteOne({ _id: id }).exec();

    res.status(200).json({ deletedId: req.params.id });
  } catch (error) {
    next(error);
  }
};
